@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,700;0,900;1,100;1,300;1,700;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
}

.row {
  margin: 1rem 10px;
}

.row .row__title {
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: 900;
}

.banner {
  padding: 1rem 2rem;
  min-height: 40vh;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  background-size: cover;
  background-position: top;
  position: relative;
  color: #fff;
}

/* overlay */
.banner .overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(57, 57, 66, 0.473);
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
}

.banner .content {
  z-index: 3;
}

.banner .banner__title {
  margin: 10px 0;
  font-size: 3rem;
  font-weight: bold;
}

.banner .banner__desc {
  max-width: 70%;
  font-size: 0.8em;
  font-weight: bold;
  line-height: 1.4rem;
  color: #fff;
}

.banner .banner__buttons {
  margin-top: 20px;
}

.banner .banner__buttons button {
  padding: 8px 10px;
  margin-right: 10px;
  outline: 0;
  border: 0;
  cursor: pointer;
  background-color: rgba(48, 48, 48, 0.678);
  transition: background-color 0.2s ease-in;
  color: #fff;
  /* border: 1px solid #969696; */
}

.banner .banner__buttons button:hover {
  background-color: rgb(26, 26, 26);
}

/* yt */
#close-btn {
  font-size: 3rem;
  background: transparent;
  outline: 0;
  color: #fff;
  border: 0;
  cursor: pointer;
}

.posters_row {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  overflow-x: scroll;
  grid-auto-flow: column;
  overflow-y: hidden;
}

.posters_row::-webkit-scrollbar {
  display: none;
}

.poster_item {
  width: 100%;
  margin-right: 10px;
  width: 200px;
  cursor: pointer;
  border: 2px solid #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  background: rgb(185, 1, 1);
  overflow: hidden;
}

.poster_item .movie_title {
  font-size: 1rem;
  font-weight: bold;
  display: inline-block;
  margin: 5px;
  color: #ffff;
}

.poster_item img {
  width: 100%;
  height: auto;
  transition: transform 0.3s;
}

.posters_row img:hover {
  transform: scale(1.09);
}

.posters_row .larger_movie_poster {
  max-height: 220px;
}

/* row css */
.overlayer {
  width: 100%;
  height: 100%;
  background: rgba(38, 38, 43, 0.952);
  position: fixed;
  top: 0;
  left: 0;
}

.d-none {
  display: none !important;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  min-height: 50px;
  background-color: rgb(0, 0, 0);
}

.nav_logo {
  width: 150px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.switch {
  position: relative;
}

.switch input {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 100;
  cursor: pointer;
}

.switch label {
  height: 30px;
  width: 70px;
  background-color: #220d0d;
  border-radius: 100px;
  display: block;
  box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.2),
    inset 0 0 5px -2px rgba(0, 0, 0, 0.4);
}

.switch label .bulb {
  height: 5px;
  width: 5px;
  background-color: #000000;
  border-radius: 50%;
  position: relative;
  top: 12px;
  left: 12px;
  display: block;
  transition: 0.5s;
}

.switch label .bulb .bulb-center {
  position: absolute;
  display: block;
  height: 10px;
  width: 10px;
  background-color: #070707;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transition: 0.7s;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 0 4px #000;
}

.switch label .bulb .bulb-center:after {
  content: '';
  display: block;
  height: 14px;
  width: 14px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  transition: 0.7s;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 2px 4px #19181b;
}

.switch label .bulb .filament-1,
.switch label .bulb .filament-2 {
  position: absolute;
  display: block;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.switch label .bulb .filament-1:after,
.switch label .bulb .filament-2:after,
.switch label .bulb .filament-1:before,
.switch label .bulb .filament-2:before {
  content: '';
  display: block;
  height: 6px;
  width: 17px;
  border-radius: 50%;
  border: 2px solid #000;
  position: absolute;
  transition: 0.7s;
  top: -4px;
  left: -2px;
  transform: rotate(-10deg);
}

.switch label .bulb .filament-1:before,
.switch label .bulb .filament-2:before {
  left: 15px;
  transform: rotate(10deg);
}
.switch label .bulb .filament-2 {
  transform: translate(-50%, -50%) rotate(45deg) !important;
}
.reflections {
  height: 100%;
  width: 100%;
  display: block;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  z-index: 90;
  perspective: 70px;
}
.reflections span {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-image: linear-gradient(-135deg), transparent 10%,
    rgba(255, 255, 255, 0.3);
  position: absolute;
  left: -40px;
  bottom: -45px;
}
.reflections span:after {
  content: '';
  display: block;
  height: 35px;
  width: 20px;
  position: absolute;
  top: -36px;
  right: -40px;
  border-radius: 50%;
  box-shadow: 4px -2px 0 -3px rgba(255, 255, 255, 0.4);
  -webkit-filter: blur(1px);
  filter: blur(1px);
  transform: rotate(-10deg);
}
.reflections:after {
  content: '';
  display: block;
  height: 80px;
  width: 50px;
  background-image: linear-gradient(
    80deg,
    rgba(255, 255, 255, 0.05) 45%,
    rgba(255, 255, 255, 0.5)
  );
  border-radius: 10% 20% 50% 30%/30% 60% 30% 40%;
  position: absolute;
  transform-style: preserve-3d;
  transform: rotateX(-25deg) rotate(-35deg) skewx(-15deg) translate(10px, -20px);
  top: -8px;
  left: -5px;
}

.switch input:checked ~ label .bulb {
  left: 55px;
  background: #a7694a;
  box-shadow: inset 0 0 1px 3px #a56758, inset 0 0 6px 8px #6b454f,
    0 20px 30px -10px rgba(0, 0, 0, 0.4), 0 0 30px 50px rgba(253, 184, 67, 0.1);
}

.switch input:checked ~ label .bulb > .bulb-center {
  background: #feed6b;
  box-shadow: inset 0 0 1px 3px #fdec6a, 0 0 12px 10px #bca83c,
    0 0 20px 14px #a1664a;
}

.switch input:checked ~ label .bulb > .bulb-center:after {
  background: #fef401;
  box-shadow: 0 0 2px 4px #fdb843;
}

.switch input:checked ~ label .bulb > .filament-1:before,
.switch input:checked ~ label .bulb > .filament-2:before,
.switch input:checked ~ label .bulb > .filament-1:after,
.switch input:checked ~ label .bulb > .filament-2:after {
  border-color: #fef4d5;
}

.switch input:checked ~ label .bulb > .sparks .spark1 {
  height: 1px;
  width: 1px;
  animation: spark1 2s ease-in-out;
  animation-delay: 0.4s;
}

.switch input:checked ~ label .bulb > .sparks .spark2 {
  height: 1px;
  width: 1px;
  animation: spark2 2.4s ease-in-out;
  animation-delay: 0.4s;
}

.switch input:checked ~ label .bulb > .sparks .spark3 {
  height: 1px;
  width: 1px;
  animation: spark3 2s ease-in-out;
  animation-delay: 0.9s;
}

.switch input:checked ~ label .bulb > .sparks .spark4 {
  height: 1px;
  width: 1px;
  animation: spark4 1.7s ease-in-out;
  animation-delay: 0.9s;
}

@keyframes spark1 {
  0% {
    right: -5px;
    height: 1px;
    width: 1px;
    opacity: 0;
  }
  20% {
    height: 3px;
    width: 3px;
    right: 0px;
    opacity: 1;
  }
  30% {
    height: 3px;
    width: 3px;
    right: -5px;
    opacity: 1;
  }
  70% {
    height: 3px;
    width: 3px;
  }
  100% {
    bottom: 40px;
    right: -60px;
    opacity: 0;
  }
}

@keyframes spark2 {
  0% {
    height: 3px;
    width: 3px;
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  100% {
    bottom: 100px;
    right: -20px;
    opacity: 0;
  }
}

@keyframes spark3 {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
    height: 2px;
    width: 2px;
  }
  100% {
    left: 0px;
    bottom: 100px;
    opacity: 0;
    height: 3px;
    width: 3px;
  }
}

@keyframes spark4 {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
    height: 2px;
    width: 2px;
  }
  100% {
    left: -20px;
    bottom: -10px;
    opacity: 0;
    height: 3px;
    width: 3px;
  }
}

